@import url(https://fonts.googleapis.com/css2?family=Inter&display=swap);
.movieGenres-genre {
  padding: 3px;
  font-size: 12px;
  margin: 0 8px 7px 0;
  line-height: 14.52px;
  background-color: #fafafa;
  border: 1px solid #d9d9d9;
  color: rgba(0, 0, 0, 0.65);
}

.movie {
  width: 470px;
  display: flex;
  height: 295px;
  overflow: hidden;
  margin-right: 36px;
  margin-bottom: 35px;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.15);
}

.movie:nth-child(2n) {
  margin-right: 0;
}

.movie-info {
  font-weight: 400;
  font-style: normal;
  font-family: 'Inter', sans-serif;
}

.img-container {
  width: 200px;
  margin-right: 20px;
}

.img-container img {
  max-width: 200px;
  height: 100%;
}

.movie-title {
  display: flex;
  justify-content: space-between;
}

.movie-title h2 {
  color: #000;
  font-size: 20px;
  line-height: 28px;
  margin: 12px 0 7px;
}

.movie-average {
  display: flex;
  min-width: 34px;
  min-height: 34px;
  align-self: start;
  border-radius: 50%;
  align-items: center;
  margin: 12px 9px 0 0;
  border: 2px solid #000;
  justify-content: center;
}

.movie-average__border-rad {
  border-color: #e90000;
}

.movie-average__border-orange {
  border-color: #e97e00;
}

.movie-average__border-yellow {
  border-color: #e9d100;
}

.movie-average__border-green {
  border-color: #66e900;
}

.movie-average p {
  margin: 0;
  height: 100%;
  font-size: 12px;
  font-weight: 700;
  line-height: 22px;
}

.movie-releaseDate {
  color: #827e7e;
  font-size: 12px;
  line-height: 22px;
  margin-bottom: 7px;
}

.movie-description {
  color: #000;
  font-size: 12px;
  line-height: 22px;
  margin-right: 20px;
}

.movie-description__mobile {
		display: none;
	}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.movieGenres {
  margin: 0;
  display: flex;
  flex-wrap: wrap;
}

.main-content {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

@media (max-width: 1024px) {
  .movie {
		height: 245px;
    margin-right: 0;
    margin-bottom: 20px;
  }

  .img-container {
    width: 60px;
    margin: 6px 13px 10px 10px;
  }

  .img-container img {
    height: auto;
    max-width: 60px;
  }

  .movie-title h2 {
    margin: 4px 0 7px;
  }

  .movie-average {
    margin: 5px 8px 0 0;
  }

  .movie-description {
		display: none;
  }

	.movie-description__mobile {
		margin-left: -70px;
		display: block;
	}

	.movie-rate{
		display: flex;
		font-size: 14px;
		margin-right: 10px;
		justify-content: flex-end;
	}
}

.main {
  margin: 32px 36px 2px;
  background-color: #fff;
}

.row {
	display: flex;
	flex-wrap: wrap;
}

@media (max-width: 420px) {
  .main {
		margin: 10px 16px 1px;
  }
}

.spinner {
  width: 40px;
  margin: 0 auto;
}

.stringRequest {
  width: 978px;
  font-size: 16px;
  padding: 9px 12px;
  line-height: 22px;
  margin: 5px 36px;
  font-family: Inter;
  border-radius: 5px;
  box-sizing: border-box;
  border: 1px solid #d9d9d9;
}

.stringRequest:focus,
.stringRequest:active {
  outline: none;
  transition: all 0.3s;
  border: 2px solid #40a9ff;
}

@media (max-width: 1024px) {
  .stringRequest {
    width: 388px;
    margin: 5px 16px;
    font-family: Inter;
    border-radius: 5px;
    box-sizing: border-box;
    border: 1px solid #d9d9d9;
  }
}

body {
  background: #e5e5e5 !important;
}

.movieApp {
  width: 1050px;
  margin: 0 auto;
  background-color: #fff;
}

.paginator {
  display: flex;
  justify-content: center;
  padding-bottom: 30px;
}

@media (max-width: 1024px) {
  .movieApp {
    width: 420px;
  }
}

